.team-section {
    text-align: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    margin-top: 132px;
}
.team-section__cards-container {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}
@media screen and (max-width: 1024px){
    .team-section {
        margin-top: 60px;
    }
}
@media screen and (max-width: 425px){
    .team-section {
        margin-top: 40px;
    }
}