.header__nav {
    position: fixed;
    z-index: 999;
    width: 100%;
    padding: 24px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header__nav--mid {
    display: flex;
    gap: 36px;
}

@media screen and (max-width: 500px){
    .header__nav {
        padding: 12px 8px;
    }
}

