.about-card {
    position: relative;
    max-width: 1100px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 80px;
    overflow: hidden;
    padding-bottom: 24px;
}
.about-card__right {
    width: 100%;
    height: 100%;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    padding: 40px 0px 8px 0px;
    justify-content: space-around;
}
.about-card__p {
    margin-bottom: 20px;
    min-height: 100px;
}

.fade-enter{
    opacity: 0;
    transform: translateX(-20px);
 }
 .fade-exit{
    opacity: 1;
    transform: translateX(0px);
 }
 .fade-enter-active{
    opacity: 1;
    transform: translateX(0px);
 }
 .fade-exit-active{
    opacity: 0;
    transform: translateX(20px);
 }
 .fade-enter-active,
 .fade-exit-active{
    transition: all 200ms;
 }

.about-card__btn-container {
    display: flex;
    gap: 16px;
}

@media screen and (max-width: 1300px){
    .about-card {
        gap: 40px;
    }
    .about-card__right {
        justify-content: center;
        padding: 0px;
    }
}
@media screen and (max-width: 1100px){
    .about-card {
        gap: 20px;
    }
}
@media screen and (max-width: 500px){
    .about-card__p {
        min-height: 160px;
    }
}
@media screen and (max-width: 400px){
    .about-card__p {
        margin-bottom: 20px;
    }
}
@media screen and (max-width: 500px){
    .section-title--about-card {
        padding-bottom: 0px!important;
    }
}
@media screen and (min-width: 500px) and (max-width: 600px){
    .about-card__right {
        gap: 36px;
    }
}
@media screen and (max-width: 1000px){
    .about-card {
        text-align: center;
        flex-direction: column;
        max-width: 600px;
    }
    .about-card__right {
        gap: 24px;
    }

    .about-card__btn-container {
        flex-wrap: wrap;
    }
    .about-card__btn-container {
        justify-content: center;
    }
}