.header {
    width: 100%;
}
.scrolled a {
    color: #fafafa;
    transition: all 0.3s;
}
.scrolled .cambio-idioma {
    color: #fafafa;
}

.scrolled .cambio-idioma__arrow{
    color: #fafafa;
}
.scrolled .cambio-idioma__idioma{
    color: #fafafa;
    transition: all 0.3s;
}
.scrolled .cambio-idioma__line{
    background-color: #fafafa;
    transition: all 0.3s;
}
.oculte-nav .header__nav--mid {
    opacity: 0;
    transition: all 0.3s;
}
.show-nav .header__nav--mid {
    opacity: 1;
    transition: all 0.3s;
}