.team-card {
    width: 240px;
    height: 480px;
}
.team-card__img-container {
    border-radius: 12px;
    overflow: hidden;
    object-fit: contain;
    background-color: black;
    box-shadow: 0px 0px 16px 0px rgba(0,0,0,0.5);
    border: 2px solid #282828;
    width: 100%;
    height: 85%;
    filter: brightness(0.9);
    transition: all 0.3s;
}
.team-card__img-container:hover {
    scale: 1.04;
    box-shadow: 0px 0px 20px 10px rgba(0,0,0,0.15);
    filter: brightness(1);
}
.team-card__img-container:hover ~ .team-card__info-container{
    transform: translateY(10px);
}
.team-card__img {
    width: 100%;
    height: 100%;
}
.team-card__info-container {
    width: 100%;
    height: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: 0.3s;
}
.team-card__name {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 4px;
}
.team-card__cargo {
    font-size: 1.4rem;
    font-weight: 500;
    color: #3B53FE;
}
@media screen and (max-width: 375px){
    .team-card {
        width: 220px;
        height: 440px;
    }
}